.gallery {
  .swiper-container {
    width: 100%;
    height: 800px;
    @include r(767) {
      height: 500px; }
    @include r(500) {
      height: 300px; }
    .swiper-slide {
      background-size: cover;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      width: 100%;
      p {
        font-size: 18px;
        text-shadow: #444444 2px 2px 10px;
        color: $white;
        margin-bottom: 140px;
        width: 70%;
        text-align: center;
        @include r(767) {
          width: 80%;
          font-size: 16px;
          margin-bottom: 70px; } } }
    .swiper-pagination {
      margin-bottom: 90px;
      display: flex;
      align-items: center;
      justify-content: center;
      @include r(767) {
        margin-bottom: 20px; } }
    .swiper-pagination-bullet {
      background: $pale-gold;
      border: 2px solid $pale-gold;
      width: 10px;
      height: 10px;
      opacity: 1; }
    .swiper-pagination-bullet-active {
      background: none;
      width: 12px;
      height: 12px; }
    .swiper-button-next, .swiper-button-prev {
      background: none;
      @include r(767) {
        display: none; }
      img {
        width: 40px; } }
    .swiper-button-prev {
      left: 85px; }
    .swiper-button-next {
      transform: rotate(180deg);
      right: 85px; } } }



