.pop-up__bg {
  position: fixed;
  z-index: 21;
  display: block;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: none;
}

.pop-up {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 22;
  display: none;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .pop-up {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    transform: none;
  }
}

.pop-up__video {
  height: 80%;
  width: 80%;
}

