  width: 100%;
  height: 100%;
}

.pop-up__close {
  width: 30px;
  height: 30px;
  padding-top: 15px;
  position: absolute;
  right: 10px;
  top: 10px;
  display: inline-block;
  cursor: pointer;
  transition: all 0.3s;
  background: #feda66;
  border-radius: 50%;
}

.pop-up__close span {
  display: block;
  transition: all 0.3s;
}

.pop-up__close span:before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%) rotate(225deg);
  width: 20px;
  height: 2px;
  background: #8a010e;
  transition: all 0.3s;
  top: 50%;
  border-radius: 20%;
}

.pop-up__close span:after {
  content: '';
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%) rotate(315deg);
  top: 50%;
  width: 20px;
  height: 2px;
  background: #8a010e;
  transition: all 0.3s;
  border-radius: 20%;
}

.pop-up__content {
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .pop-up__content {
    width: 100%;
    overflow: scroll;
    display: flex;
    align-items: center;
  }
