.standards {
  padding-bottom: 100px;
  &__content {
    max-height: 360px;
    margin-bottom: 50px;
    @include r(767) {
      max-height: 100%; } }
  &__calculator {
    display: flex;
    align-items: center;
    height: 100%;
    @include r(767) {
      flex-wrap: wrap;
      height: auto; }
    @include r(380) {
      flex-wrap: nowrap; }
    &__range {
      position: relative;
      width: 50px;
      height: 360px;
      padding-left: 10px;
      @include r(767) {
        margin-left: 20px; }
      @include r(380) {
        margin-left: 0; }
      input[type="range"] {
        width: 100% !important; }
      .rangeslider {
        border-radius: 3px;
        background: $brownish-grey;
        width: 4px;
        height: 100%; }
      .rangeslider__fill {
        border-radius: 3px;
        background: $pale-gold;
        height: 100%; }
      .rangeslider__handle {
        width: 30px;
        height: 10px;
        border-radius: 3px;
        background: $pale-gold;
        box-shadow: 0 2px 4px 0 rgba(138, 1, 14, 0.15);
        border: none;
        left: -13px;
        &:before {
          content: '';
          width: 100%;
          height: 30px;
          background: transparent;
          position: absolute;
          top: -10px; } }
      .rangeslider__handle:after {
        display: none; } }
    &__info {
      max-width: 280px;
      width: 100%;
      margin-left: 50px;
      @include r(500) {
        margin-left: 10px;
        width: auto; }
      @include r(380) {
        margin-left: 0; }
      &__title {
        margin-bottom: 20px;
        min-height: 75px;
        h4 {
          font-size: 30px;
          font-weight: bold;
          color: $crimson;
          transition: all 0.3s;
          @include r(380) {
            font-size: 25px; } } }
      p {
        font-size: 18px;
        line-height: 1.33;
        color: $brownish-grey;
        transition: all 0.3s;
        @include r(380) {
          font-size: 15px; }
        span {
          color: $crimson;
          margin-right: 4px; } } } }
  &__img {
    margin-left: 80px;
    height: 360px;
    display: flex;
    align-items: flex-end;
    @include r(850) {
      margin-left: 25px; }
    @include r(767) {
      margin-top: 40px;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: flex-end; }
    @include r(440) {
      display: none; }
    &__first {
      min-width: 230px;
      display: flex;
      justify-content: flex-end;
      img {
        height: 300px;
        transition: all 0.3s ease-in-out; } }
    &__last {
      img {
        height: 190px;
        margin-left: 20px; } } }
  &__description {
    display: flex;
    flex-direction: column;
    align-items: center;
    p {
      font-size: 30px;
      line-height: 1.2;
      text-align: center;
      color: $brownish-grey;
      margin-bottom: 50px;
      max-width: 970px;
      @include r(767) {
        font-size: 22px; } } } }
