.fabrication {
  padding-bottom: 110px;
  @include r(767) {
    margin-bottom: 0; }
  img {
    width: 290px;
    position: relative; }
  &__step {
    position: relative;
    @include r(767) {
      margin-bottom: 60px; }
    h3 {
      font-size: 30px;
      font-weight: bold;
      color: $crimson;
      margin-bottom: 16px; }
    p {
      font-size: 27px;
      line-height: 1.2;
      color: $brownish-grey;
      @include r(767) {
        font-size: 20px; } } } }
