.reliability {
  padding-bottom: 100px;
  .swiper {
    position: relative; }
  .swiper-container {
    height: 390px;
    padding: 10px 10px;
    .swiper-slide {
      overflow: hidden;
      width: 100%;
      border-radius: 10px;
      position: relative;
      transition: all 0.3s;
      @include r(1024) {
        box-shadow: 0 5px 10px 0 rgba(138, 1, 14, 0.15); }
      a {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center; }
      &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgba(255, 249, 238, 0.5);
        transition: all 0.3s;
        visibility: visible;
        opacity: 1;
        @include r(1024) {
          display: none; } }
      &:hover {
        box-shadow: 0 5px 10px 0 rgba(138, 1, 14, 0.15);
        &:before {
          visibility: hidden;
          opacity: 0; } } } }
  .swiper-button-next, .swiper-button-prev {
    background: none;
    @include r(1024) {
      display: none; }
    img {
      width: 40px; } }
  .swiper-button-prev {
    left: -55px;
    @include r(1270) {
      left: 10px; } }
  .swiper-button-next {
    transform: rotate(180deg);
    right: -55px;
    @include r(1270) {
      right: 10px; } } }
